@import 'src/common/styles/mixins';
@import 'src/common/styles/variables';

.header {
  @include flex(row, space-between, center);
  max-width: 100%;
  margin-left: 25px;

  @media screen and (max-width: 680px) {
    display: none;
  }

  &_title {
    color: $textColor;
    min-width: 200px;
  }
}